.contact-us {
  &__info {
    margin: 0 0 24px;

    &--last{
      margin-bottom: 32px;
    }

  }

  &__info-title {
    margin: 0 0 8px;

    font-size: 14px;
    color: #9FADC2;
  }

  &__info-link {
    color: inherit;
    text-decoration: none;
    transition: color 0.5s;

    &:hover {
      color: $color-actient-blue;
    }
  }

  &__field {
    margin: 0 0 16px;

    &--last {
      margin: 0 0 32px;
    }
  }
}
