.page {
  font-family: "Roboto", sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: $paragraf-line-hight;
  color: $color-page-text-gray;

  scroll-behavior: smooth;

  &__body {
    margin: 0;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(-100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 64px;

    @include onDesctop {
      margin-bottom: 104px;
    }
  }

  &__section--title {
    margin: 0 0 32px;
    
    font-weight: bold;
    font-size: 36px;
    line-height: 36px;
    text-align: center;
    letter-spacing: -1.5px;
    color: #000;

    @include onDesctop {
      margin-bottom: 56px;
      font-size: 48px;
      line-height: 48px;
    }

    &::after {
      content: "";
      display: block;
      height: 16px;
      width: 23px;
      margin: 4px auto 0;
      background:no-repeat center url(../images/crown.svg);
    }
  }
}

.page__body--with-menu {
  overflow: hidden;
}
